body {
  background: #000000 url(../img/header-bg.jpg) no-repeat center top;
  font-size: 14px;
  line-height: 24px;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  color: #fff;

  .btn {
    display: inline-block;
    background-color: #000;
    color: #fff;
    font-size: 14px;
    line-height: 24px;
    font-family: "PT Sans Narrow", "Arial", sans-serif;
    font-weight: bold;
    padding-top: 13px;
    padding-bottom: 13px;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    border: none;
    width: 140px;
    &:hover,
    &:active {
      background-color: #663d15; } }

  .modal_form {
    display: none;
    z-index: 10;
    position: absolute;
    width: 460px;
    background-color: #f9f6f3;
    color: #000;
    top: 120px;
    // transform: translate(-50%, 0%)
    margin-left: -230px;
    left: 50%;
    padding: 50px 80px;
    box-shadow: 0 30px 50px rgba(0, 0, 0, 0.5);
    h2 {
      font-size: 30px;
      line-height: 42px;
      margin-bottom: 22px; }
    p {
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 24px; }
    input[type="text"],
    input[type="password"] {
      width: 300px;
      background-color: #f9f6f3;
      border: 2px solid #000;
      padding: 10px 10px;
      padding-right: 40px;
      margin-bottom: 9px;
      color: #000;
      font-size: 10px;
      line-height: 18px;
      font-family: "PT Sans Narrow", "Arial", sans-serif;
      font-weight: bold;
      text-transform: uppercase;
      &:focus {
        border-color: #663d15; } }
    .login_checkbox {
      position: relative;
      float: left;
      padding-left: 30px;
      margin-bottom: 15px;
      input[type="checkbox"] {
        display: none; }
      .checkbox_indicator {
        display: inline-block;
        position: absolute;
        left: 0;
        top: 2px;
        height: 20px;
        width: 20px;
        border: 2px solid #000; }
      :checked + .checkbox_indicator {
        &::after,
        &::before {
          content: '';
          position: absolute;
          width: 16px;
          height: 2px;
          background-color: #000;
          top: 7px;
          left: 0px; }

        &::after {
          transform: rotate(45deg); }

        &::before {
          transform: rotate(-45deg); } } }
    a {
      float: right;
      color: #000; }
    .clear {
      clear: both; }
    .btn {
      width: 300px; } }

  @keyframes bounce {
    0% {
      transform: translateY(-2000px); }
    70% {
      transform: translateY(30px); }
    90% {
      transform: translateY(-10px); }
    100% {
      transform: translateY(0); } }

  .modal_form_active {
    display: block;
    animation: bounce 0.6s; }

  @keyframes shake {
    0%, 100% {
      transform: translateX(0); }
    10%, 30%, 50%, 70%, 90% {
      transform: translateX(-10px); }
    20%, 40%, 60%, 80% {
      transform: translateX(10px); } }

  .modal_form_error {
    animation: shake 0.6s; }

  .modal_map {
    display: none;
    z-index: 11;
    position: fixed;
    bottom: 50px;
    left: 50%;
    transform: translate(-50%, 0%);
    height: 575px;
    width: 780px;
    border: 7px solid #fff; }

  .modal_map_active {
    display: block; }

  .modal_close {
    position: absolute;
    height: 25px;
    width: 25px;
    top: 8px;
    right: -35px;
    padding: 0;
    border: 0;
    background-color: transparent;
    &::before {
      content: '';
      width: 27px;
      height: 5px;
      top: 10px;
      left: -1px;
      transform: rotate(45deg);
      background-color: rgba(256, 256, 256, 0.8);
      position: absolute; }
    &::after {
      content: '';
      width: 27px;
      height: 5px;
      top: 10px;
      left: -1px;
      transform: rotate(-45deg);
      background-color: rgba(256, 256, 256, 0.8);
      position: absolute; } }

  .modal_overlay_active {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background: rgba(0, 0, 0, 0.5); }


